

.work-container {
    z-index: -1;
    overflow-y: visible;
    position: absolute;
    width: 100%;
    color: white;
    margin-top: 7em;
    transform-origin: left;
  
}


.work-container h1{
    text-align: center;
    font-size: 3em;
    margin-top: 0
}

.project-wrapper {
    display: flex;
    justify-content: space-evenly;
    margin: 3em auto;
    margin-bottom: 10em;
    perspective: 1000px;
    width: 90%;
    transform: scale(1.3);
    
}

.title-btn {
    display: flex;
    justify-content: space-evenly;
    margin-top: 2em;
}

.title-btn h1 {
    background-image:  linear-gradient(0deg, rgba(255,255,255,1) 0%, rgba(238,0,0,1) 100%);
    background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-bottom: 0;
}
.title-btn a {
    height: fit-content;
}

.title-btn button {
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.15);
    background-color: transparent;
    font-size: 2em;
    color: var(--primary);
    color: white;
    border: 3px solid var(--primary);
    outline: none;
    border-radius: 10px;
    padding: 10px 30px;
    /* backdrop-filter: blur(2px); */
    /* background-clip: padding-box; */
    box-shadow:5px 5px 10px rgba(255, 255, 255, 0.2);
}


.project-wrapper:nth-child(even){
    flex-direction: row-reverse;
}

.project-wrapper .preview{
    width: 500px;
    height: 300px;
    border: 2px solid transparent;
    box-shadow:0px 0px 20px rgba(255, 255, 255, 0.2);
    transform: scale(1.2);
    border-radius: 20px;
    background-color: black;
    overflow: hidden;
    transition: transform 400ms 300ms ease-in-out;
}

.img-container {
    width: 1500px;
    height: 300px;
    /* opacity: 0; */
}

.project-img {
    width: 500px;
    height: 300px;
    animation: slide 20s ease-in-out infinite alternate;
    transition: all 500ms ease-in-out;
    /* opacity: 0; */
}

.project-wrapper .project-content{
    width: 40%;
    /* height: 40%; */
}

.project-content h2 {
    font-size: 2em;
    color: var(--primary);
    margin: 0;
}

.project-content p {

    font-size: 1.5em;
    line-height: 1.5rem;
}

.project-content button{
    font-size: 1.7em;
    margin: 0 10px;
    color: white;
    /* background: var(--primary); */
   
    /* background-clip: text;
    -webkit-text-fill-color: transparent; */
    padding: 10px 30px;
    border-radius: 30px;
    border: none;
    outline: none;
    cursor: pointer;
    user-select: none;
    transition: 100ms ease-in-out;
}

.project-content button:active{
    transform: scale(0.9);
}

.project-content {
    transition: transform 400ms ease-in-out;
    background-color: rgba(255, 255, 255, 0.15);
    padding: 10px 20px;
    border-radius: 20px;
    backdrop-filter: blur(5px);
    border: 2px solid transparent;
    background-clip: padding-box;
    box-shadow:0px 0px 20px rgba(255, 255, 255, 0.2);
}


/*#####################################################
######################################################
this section is for the 3d effect
################################################
####################################################*/

.project-wrapper:nth-child(odd) .preview{
    transform-style: preserve-3d;
    transform: rotateY(35deg);
    animation: tilt-left 8s ease-in-out infinite alternate ;
}

.project-wrapper:nth-child(even) .preview{
    transform-style: preserve-3d;
    transform: rotateY(-35deg);
    animation: tilt-right 8s ease-in-out infinite alternate ;
}

.project-wrapper:nth-child(odd) .project-content {
    transform-style: preserve-3d;
    transform: rotateY(-35deg);
    animation: tilt-right 8s ease-in-out infinite alternate ;
}

.project-wrapper:nth-child(even) .project-content {
    transform-style: preserve-3d;
    transform: rotateY(35deg);
    animation: tilt-left 8s ease-in-out infinite alternate ;
    
}

/* ###########################################################
end of 3d effect
################################################################*/

@keyframes slide {
    0% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(-500px);
    }

    100% {
        transform: translateX(-1000px);
    }
}

@keyframes tilt-left {
    0% {
        transform: rotateY(35deg);
    }
    45% {
        transform: rotateY(35deg);
    }
    55% {
        transform: rotateY(0);
    }
    100% {
        transform: rotateY(0);
    }
}

@keyframes tilt-right {
    0% {
        transform: rotateY(-35deg);
    }
    45% {
        transform: rotateY(-35deg);
    }
    55% {
        transform: rotateY(0);
    }
    100% {
        transform: rotateY(0);
    }
}

@media (max-width:1000px) {

    .project-wrapper {
        display: inline-block;
        padding-top: 50px;
    }

  
    .project-wrapper .preview{
        margin: 0 auto;
    }
   .project-wrapper .project-content {
        width: 60%;
        margin: 50px auto;
    }
}

@media (max-width:800px) {
    .project-wrapper .project-content {
        width: 70%;
        font-size: 0.6em;
    }
   
}

@media (max-width:600px) {
   .project-wrapper .preview {
        width: 400px;
        height: 250px;
        border: 2px solid transparent;
        box-shadow:0px 0px 20px rgba(255, 255, 255, 0.2);
        transform: scale(1.2);
        border-radius: 20px;
        background-color: black;
        /* overflow: hidden; */
        transition: transform 400ms 300ms ease-in-out;
    }

    .project-wrapper .img-container {
        width: 1200px;
        height: 250px;
    }
    .project-wrapper .project-img {
        width: 400px;
        height: 250px;
        animation: slide-600 20s ease-in-out infinite alternate;
        transition: all 500ms ease-in-out;
    }

}

@media (max-width:550px) {
    .title-btn h1 {
        font-size: 2.5em;
    }
    .title-btn a {
        font-size: 0.7em;
    }
}

@media (max-width:500px) {
    .title-btn h1 {
        font-size: 2em;
    }
    .title-btn a {
        font-size: 0.5em;
    }
}

@media (max-width:450px) {
    .project-wrapper .preview {
        width: 250px;
        height: 180px;
        border: 2px solid transparent;
        box-shadow:0px 0px 20px rgba(255, 255, 255, 0.2);
        transform: scale(1.2);
        border-radius: 20px;
        background-color: black;
        /* overflow: hidden; */
        transition: transform 400ms 300ms ease-in-out;
    }

    .project-wrapper .img-container {
        width: 750px;
        height: 180px;
    }
    .project-wrapper .project-img {
        width: 250px;
        height: 180px;
        animation: slide-450 20s ease-in-out infinite alternate;
        transition: all 500ms ease-in-out;
    }

    
}


@media (max-width:500px) {
    .title-btn h1 {
        font-size: 1.5em;
    }
    .title-btn a {
        font-size: 0.3em;
    }
}



@keyframes slide-600 {
    0% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(-400px);
    }

    100% {
        transform: translateX(-800px);
    }
}

@keyframes slide-450 {
    0% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(-250px);
    }

    100% {
        transform: translateX(-500px);
    }
}



