   @keyframes bounce1 {
       0% {
        transform: scale(1);
        color: var(--primary);
       }

       90% {
            transform: scale(1);
           color: var(--primary);
       }
       91% {
           color: white;
       }

       100% {
           transform: scale(1.2);
           color: white;
       }
   }
   @keyframes bounce2 {
       0% {
        transform: scale(1);
        color: var(--primary);
       }

       80% {
            transform: scale(1);
           color: var(--primary);
       }
       91% {
        color: white;
    }

    100% {
        transform: scale(1.2);
        color: white;
    }
   }
   @keyframes bounce3 {
       0% {
        transform: scale(1);
        color: var(--primary);
       }

       80% {
            transform: scale(1);
           color: var(--primary);
       }
       91% {
        color: white;
    }

    100% {
        transform: scale(1.2);
        color: white;
    }
   }
   @keyframes bounce4 {
       0% {
        transform: scale(1);
        color: var(--primary);
       }

       80% {
            transform: scale(1);
           color: var(--primary);
       }
       91% {
        color: white;
    }

    100% {
        transform: scale(1.2);
        color: white;
    }
   }

   @media (max-width:1000px) {
       #about-container {
           flex-direction: column;
           /* padding-top: 100px; */
       }

       .icons {
           display: flex;
           flex-direction: row;
           justify-content: space-evenly;
       }

       .skills ul {
           display: grid;
           grid-template-columns: repeat(4, 1fr);
       }

       .bio, .skills {
           margin: 0 auto;
           width: 90%;
       }
   }

   @media (max-width:700px) {
       #about-container {
           font-size: 0.75em;
           /* padding-top: 100px; */
       }
   }

   @media (max-width:400px) {
    .bio, .skills {
        margin: 0 auto;
        width: 80%;
    }

    .skills ul {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
    }
    #about-container {
        flex-direction: column;
        padding-top: 100px;
    }

   }

   @media (max-height:800px) {
    #about-container {
        padding-top: 100px;
    }
   }



   @media (max-height:700px) {
       #about-container {
           padding-top: 120px;
       }
   }

   @media (max-height:600px) {
      #about-container {
          padding-top: 220px;
      }
   }