#particles-home{
  position: relative;
  animation: bg 2s 1s ease-out forwards;
    opacity: 0;
   z-index: -1;
}

#root {
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
}

@keyframes bg {
  from {
    opacity: 0;
  }
  to{
    opacity: 1;
  }
}

#container{
    overflow: hidden;
    left: 50%;
    transform: translateX(-50%);
    width: 100vw;
    position: absolute;
    margin: 0 auto;
    height: calc(100vh - 8em);
    color: var(--primary);
    margin-top: 8em;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    transform-origin: left;
    z-index: 500;
    overflow-y: hidden;
}

#container h1{
    position: relative;

    text-align: center;

    font-size: 4em;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;

}

#container h1 span{
    color: white;
}

#container h1 div {
    margin: 10px 0;
}

#container h1 div:nth-child(1){
  opacity: 0;
    animation: home 600ms 0.7s ease-in-out forwards;
}
#container h1 div:nth-child(2){
    opacity: 0;
     animation: home 600ms 0.8s ease-in-out forwards;
    font-size: 0.8em;
}
#container h1 div:nth-child(3){
    opacity: 0;
    animation: home 600ms 0.9s ease-in-out forwards;
}

#button-container{
    position: relative;

    width: -webkit-fit-content;

    width: -moz-fit-content;

    width: fit-content;
    margin: 50px auto;
}

#button-container button{
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    outline: none;
    font-size: 3em;
    border-radius: 150px;
    margin: 25px;
    color: white;
    border: none;
    padding: 20px 60px;
    background-color: var(--primary);
    position: relative;
   
    transition: color 200ms ease-in-out, transform 100ms ease-in-out;
    opacity: 0;
    animation: button 600ms 1s ease-in-out forwards, bounce 500ms 1.6s ease-in-out alternate infinite;
}

#button-container button:hover{
    cursor: pointer;
    transform: scale(1.05);
 
}

#button-container button:active{
    transform: scale(0.95);
    /* animation: smalle 500ms ease-in-out ; */
}


