 @keyframes home {
     0%{ 
         opacity: 0;
         overflow-y: hidden;
         transform: translateY(50px) rotateZ(45deg);
        transform-origin: left;
     }

     50%{
         opacity: 0;
     }

     100% {
        transform-origin: left;
        opacity: 1;
        overflow-y: hidden;
        transform: translateY(0px) rotateZ(0);
       
     }
 }

 @keyframes button {
     0% {
         opacity: 0;
         transform: scale(0);
     }

     80% {
         transform: scale(1.3);
         opacity: 1;
     }

     100% {
         opacity: 1;
         transform: scale(1);
     }
 }

@keyframes bounce {
    0% {
        transform: scale(0.95);
        opacity: 1;
    }

    50% {
        transform: scale(1.05);
        opacity: 1;
    }
    100% {
        transform: scale(1);
        opacity: 1;
    }
}

