/*#####################################################
######################################################
this section is for the 3d effect
################################################
####################################################*/

.project-wrapper:nth-child(odd) .preview{
    transform-style: preserve-3d;
    transform: rotateY(35deg);
    animation: tilt-left 8s ease-in-out infinite alternate ;
}

.project-wrapper:nth-child(even) .preview{
    transform-style: preserve-3d;
    transform: rotateY(-35deg);
    animation: tilt-right 8s ease-in-out infinite alternate ;
}

.project-wrapper:nth-child(odd) .project-content {
    transform-style: preserve-3d;
    transform: rotateY(-35deg);
    animation: tilt-right 8s ease-in-out infinite alternate ;
}

.project-wrapper:nth-child(even) .project-content {
    transform-style: preserve-3d;
    transform: rotateY(35deg);
    animation: tilt-left 8s ease-in-out infinite alternate ;
    
}

/* ###########################################################
end of 3d effect
################################################################*/

@keyframes slide {
    0% {
        transform: translateX(0);
    }

    50% {
        transform: translateX(-500px);
    }

    100% {
        transform: translateX(-1000px);
    }
}

@keyframes tilt-left {
    0% {
        transform: rotateY(35deg);
    }
    45% {
        transform: rotateY(35deg);
    }
    55% {
        transform: rotateY(0);
    }
    100% {
        transform: rotateY(0);
    }
}

@keyframes tilt-right {
    0% {
        transform: rotateY(-35deg);
    }
    45% {
        transform: rotateY(-35deg);
    }
    55% {
        transform: rotateY(0);
    }
    100% {
        transform: rotateY(0);
    }
}

