        background-color: rgba(0, 0, 0, 1);
        transition:  500ms ease-in ;
        width: 200px;
        height: 100%;
    }

    .open {
        position: absolute;
        right: 0;
        top: 0;
        background-color: rgba(0, 0, 0, 1);
        transition: 500ms ease-out ;
        width: 200px;

    }

    .menu-open-icon {
        display: inline-block;
        color: var(--primary);
        margin-left: 85%;
        font-size: 3em;
        margin-top: 20px;
        cursor: pointer;
    }

    .menu-close-icon {
        display: block;
        color: var(--primary);
        margin-left: 70%;
        font-size: 4em;
        margin-top: 20px;
        cursor: pointer;
    }
}


:root{
    --primary: rgb(238, 0, 0);
    --secondary: rgb(214, 214, 214);
    --primary3D: rgb(139, 0, 0);
    --secondary3D: rgb(180, 180, 180);
}

body{
    font-family: "Montserrat", sans-serif;
    margin: 0;
    background-color: black;
    min-width: 100vw;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}



