header {
    top: 0;
    position: fixed;
    width: 100%;
    /* background-color: black; */
    /* border-bottom: 2px solid white; */
    /* background-color: rgba(255, 255, 255, 0.1); */
    background-color: black;
    z-index: 500;
   
}

.menu-close-icon {
    display: none;
}

/* .menu-open-icon {
    display: none;
} */

header ul {
    padding: 0;
    margin: 0;
    color: var(--primary);
    display: flex;
    list-style: none;
    justify-content: space-evenly;
    font-size: 2em;
    color: var(--primary);
   
}

header ul li{
   
    /* width: 100%; */
    padding: 20px 0;
    transition: color 200ms ease-in-out, transform 100ms ease-in-out;
    user-select: none;
    cursor: pointer;
  
}
.nav{
    opacity: 0;
    text-decoration: none;
    color: var(--primary);
}

header ul li:hover {
    /* background-color: var(--primary); */
    transform: scale(1.1);
    color: white;
}

header ul li:active{
    transform: scale(1);
}

.is-active {
    color: white;
    font-size: 1.2em;
    /* font-weight: bolder; */

}

.nav:nth-child(1) {
    animation: header 500ms ease-out forwards;
  
}
.nav:nth-child(2) {
    animation: header 500ms 100ms  ease-out forwards;
}
.nav:nth-child(3) {
    animation: header 500ms 200ms ease-out forwards;
}
.nav:nth-child(4) {
    animation: header 500ms 300ms ease-out forwards;
}
.nav:nth-child(5) {
    animation: header 500ms 400ms ease-out forwards;
}


@keyframes header {
    from {
        transform: translateY(-40px);
        opacity: 0;
    }
    to {
        transform: translateY(0);
        opacity: 1;
    }
}


@media (max-width:800px){
    header {
        font-size: 0.75em;
        
    }
}

@media (max-width:600px){
    nav ul {
        flex-direction: column;
        text-align: center;
        min-height: fit-content;
        height: 100vh;
       
    }
    nav ul li {
        padding: 0;
    }

    .closed {
        position: absolute;
        right: -100%;
        top: 0;
        background-color: rgba(0, 0, 0, 1);
        transition:  500ms ease-in ;
        width: 200px;
        height: 100%;
    }

    .open {
        position: absolute;
        right: 0;
        top: 0;
        background-color: rgba(0, 0, 0, 1);
        transition: 500ms ease-out ;
        width: 200px;

    }

    .menu-open-icon {
        display: inline-block;
        color: var(--primary);
        margin-left: 85%;
        font-size: 3em;
        margin-top: 20px;
        cursor: pointer;
    }

    .menu-close-icon {
        display: block;
        color: var(--primary);
        margin-left: 70%;
        font-size: 4em;
        margin-top: 20px;
        cursor: pointer;
    }
}

